/** Has a mobile first approach.
 ** Desktop code in media query 
** /

/** Variables */
:root {
  --primary: #35385e;
  --secondary: #1e90ff;
  --white: #ffffff;
  --black: #000000;
  --gray: #b6b6b6;

  --button-border-radius: 0.3rem;
  --button-padding: 0.5rem;
  --button-margin: 1rem;

  --input-padding: 0.3rem 0.3rem 0.2rem 0.2rem;

  --label-font-size: 12px;

  --footer-font-size: 12px;
}

/** Globals */
body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, Sans-Serif;
  background-color: var(--black);
}

input {
  padding: var(--input-padding);
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid var(--gray);
  width: 100%;
}

input:focus {
  outline: none;
  border-bottom: 1px solid var(--secondary);
}

label {
  font-size: var(--label-font-size);
  display: block;
  text-align: left;
  margin: 5px 5px 5px 0px;
}

button {
  border-radius: var(--button-border-radius);
  padding: var(--button-padding);
}
button.primary {
  color: var(--white);
  background-color: var(--primary);
}
button.secondary {
  color: var(--white);
  background-color: var(--secondary);
}
button:disabled {
  color: var(--white);
  background-color: var(--gray);
}

footer {
  font-size: var(--footer-font-size);
}

.container {
  position: relative;
  width: auto;
  height: 100vh;
  margin: auto;
  background-color: var(--white);
}
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.primary {
  color: var(--primary);
}

.secondary {
  color: var(--secondary);
}

.action {
  cursor: pointer;
  text-transform: uppercase;
  font-size: 12px;
}

/** Grid 
 ** Todo - Offset 
 */
* {
  box-sizing: border-box;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

[class*="col-"] {
  float: left;
  padding: 8px;
}

/* For Mobile first Grid: */
.col-1 {
  width: 8.33%;
}
.col-2 {
  width: 16.66%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33%;
}
.col-5 {
  width: 41.66%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33%;
}
.col-8 {
  width: 66.66%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33%;
}
.col-11 {
  width: 91.66%;
}
.col-12 {
  width: 100%;
}

/** Desktop */
@media only screen and (min-width: 768px) {
  .container {
    position: relative;
    height: 90vh;
    width: 40%;
    margin: 5vh auto auto auto;
  }
  /* For desktop: */
  .col-lg-1 {
    width: 8.33%;
  }
  .col-lg-2 {
    width: 16.66%;
  }
  .col-lg-3 {
    width: 25%;
  }
  .col-lg-4 {
    width: 33.33%;
  }
  .col-lg-5 {
    width: 41.66%;
  }
  .col-lg-6 {
    width: 50%;
  }
  .col-lg-7 {
    width: 58.33%;
  }
  .col-lg-8 {
    width: 66.66%;
  }
  .col-lg-9 {
    width: 75%;
  }
  .col-lg-10 {
    width: 83.33%;
  }
  .col-lg-11 {
    width: 91.66%;
  }
  .col-lg-12 {
    width: 100%;
  }
}
